@import "../../wui-core/scss/minxin-variables";
@import "../../wui-core/scss/minxin-mixins";
@import "../../wui-icon/src/Icon.scss";

$modal-prefix-cls: $prefix-cls + 'modal';

$modal-header-padding:9px;
$modal-body-padding : 16px;
$modal-footer-padding : 10px 24px;
.#{$modal-prefix-cls} {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $zindex-modal;
  pointer-events: none;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  overflow: hidden;

  *, :after, :before {
    box-sizing:border-box;
  }
  &-mask {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    //z-index: $zindex-modal;
    background: $modal-backdrop-bg;
    width: 100%;
    height: 100%;
    opacity: 1;
    pointer-events: all;
  }
  &-draggable{//可拖拽移动的标识
    .#{$modal-prefix-cls}-header.dnd-handle:hover{
      cursor: all-scroll;
    }
  }
  &-centered {//垂直距中的标识
    text-align: center;
    //&::before {
    //  display: inline-block;
    //  width: 0;
    //  height: 100%;
    //  vertical-align: middle;
    //  content: '';
    //}
    .#{$modal-prefix-cls}-dialog {
      top: 0;
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  //&-centered.draging{
  //  text-align: unset;
  //  &::before{
  //    display: none;
  //  }
  //}
  //&-centered.draged{
  //  text-align: unset;
  //  &::before{
  //    display: none;
  //  }
  //}
  &-open{//打开的状态
    overflow: hidden;
  }
  &-resizbox{//调整尺寸的box
    pointer-events: all;
    &::before {
      content: '';
      width: 16px;
      height: 1px;
      border: none;
      background-color: transparent;
      position: absolute;
      right: 0;
      bottom: 7px;
      z-index: 1;
      transform: rotate(-45deg);
      border-top: 1px solid #ccc;
      border-right: 1px solid transparent;
      border-left: 1px solid transparent;
    }
    &::after {
      content: '';
      width: 8px;
      height: 1px;
      border: none;
      background-color: transparent;
      position: absolute;
      right: 1px;
      bottom: 4px;
      z-index: 1;
      transform: rotate(-45deg);
      border-top: 1px solid #ccc;
      border-right: 1px solid transparent;
      border-left: 1px solid transparent;
    }
  }
  //&.fade {
  //  opacity: 0;
  //  transition: opacity 0.15s linear;
  //}
  //&.fade.in {
  //  opacity: 1;
  //  background: $modal-backdrop-bg;
  //}
  //&.fade .#{$modal-prefix-cls}-dialog {
  //  -webkit-transform: translateY(-25%);
  //  transform: translateY(-25%);
  //  -webkit-transition: -webkit-transform .3s ease-out;
  //  transition: transform .3s ease-out;
  //}
  //&.in .#{$modal-prefix-cls}-dialog {
  //  -webkit-transform: translate(0);
  //  transform: translate(0);
  //  &.#{$modal-prefix-cls}-draging,&.#{$modal-prefix-cls}-draged{
  //    margin: 0;
  //  }
  //}

  .#{$modal-prefix-cls}-content {
    position: absolute;
    background-color: #fff;
    border: 0;
    border-radius: $border-radius-base;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    background-clip: padding-box;
    outline: 0;
    display: flex;
    flex-direction: column;
    pointer-events: all;
  }

  .#{$modal-prefix-cls}-dialog {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    padding: 0;
    //确保content内容高度超过可视区域时显示滚动条
    overflow: auto;
    pointer-events: none;
  }
  .#{$modal-prefix-cls}-header {
    padding: $modal-header-padding;
    border-bottom: 1px solid $modal-header-border-color;
    color: $font-color-base;
    .close {
      margin-top: -3px;
      margin-right: 11px;
      float: right;
      font-size: 21px;
      font-weight: 700;
      line-height: 1;
      color: #000;
      text-shadow: 0 1px 0 #fff;
      padding: 0;
      cursor: pointer;
      outline: none;
      background: transparent;
      border: 0;
      -webkit-appearance: none;
      .uf-close{
        font-size: 13px;
        zoom: 0.8;
        color: #999;
        &:hover{
          color: #e23;
        }
      }
    }
    .maximize {
      margin-top: -1px;
      float: right;
    }
  }

  .#{$modal-prefix-cls}-title {
    padding-left: 7px;
    margin: 0;
    line-height: 1.42857143;
    font-weight: 400;
    font-size: 14px;
  }
  .#{$modal-prefix-cls}-body {
    overflow-y: auto;
    position: relative;
    padding: $modal-body-padding;
    font-size: 12px;
    color: $font-color-base;
    flex: 1;
    outline: 0;
    -ms-flex: auto;
  }
  .#{$modal-prefix-cls}-footer {
    padding: $modal-footer-padding;
    text-align: right;
    border-top: 1px solid $modal-footer-border-color;

    button {
      &:not(:last-child) {
        margin-right: 8px; // 操作按钮的间距是8px
      }
    }
  }
  &-confirm.#{$modal-prefix-cls}{
    .#{$modal-prefix-cls}-title> .uf{
      font-size: 32px;
      line-height: 32px;
      padding: 0;
      margin-right: 16px;
      float: left;
      font-weight: 500;
    }
    .#{$modal-prefix-cls}-header {
      padding-bottom: 4px;
    }
    .#{$modal-prefix-cls}-body {
      padding-top: 4px;
      .#{$modal-prefix-cls}-confirm-content{
        padding-left: 48px;
        p{
          margin: 0;
        }
      }
    }
  }
  &-confirm-info .#{$modal-prefix-cls}-title> .uf{
    color: $brand-info;
  }
  &-confirm-success .#{$modal-prefix-cls}-title> .uf{
    color: $brand-success;
  }
  &-confirm-warning .#{$modal-prefix-cls}-title> .uf{
    color: $brand-warning;
  }
  &-confirm-error .#{$modal-prefix-cls}-title> .uf{
    color: $brand-danger;
  }
  &-confirm-confirm .#{$modal-prefix-cls}-title> .uf{
    color: $brand-warning;
  }

  @media (min-width: 768px) {
    //.#{$modal-prefix-cls}-dialog {
    //  width: $modal-width-default;
    //  margin: 30px auto;
    //}
    .#{$modal-prefix-cls}-content {
      width: $modal-width-default;
      margin: 0;
      padding: 0;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
    }
    .#{$modal-prefix-cls}-sm {
      .#{$modal-prefix-cls}-content {
        width: $modal-width-sm;
        min-height: $modal-min-height-sm;
        max-height: $modal-max-height-sm;
      }
    }
    .#{$modal-prefix-cls}-md {
      .#{$modal-prefix-cls}-content {
        width: $modal-width-default;
        height: $modal-height-default;
      }
    }
    .#{$modal-prefix-cls}-lg {
      .#{$modal-prefix-cls}-content {
        width: $modal-width-lg;
        height: $modal-height-lg;
      }
    }
    .#{$modal-prefix-cls}-xlg {
      .#{$modal-prefix-cls}-content {
        width: $modal-width-xlg;
        height: $modal-height-xlg;
      }
    }
  }

  // @media (min-width: 992px) {
  // .#{$modal-prefix-cls}-lg {
  //   width: $modal-lg;

  //   .#{$modal-prefix-cls}-content {
  //     height: 560px;
  //   }
  // }
  // }

  @media (min-width: 1200px) {
    .#{$modal-prefix-cls}-xlg {
      .#{$modal-prefix-cls}-content {
        width: $modal-width-xlg;
      }
    }
  }
}

