.ac-confirm{
  .#{$modal-prefix-cls}-dialog{
      //width: 410px;
      //height: 201px;
      .ac-confirm-footer{
          height: 52px;
          padding: 0 32px 24px 0;
          border-top: none;
          .#{$prefix-cls}button{
            min-width: 60px;
            min-height: 28px;
            line-height: 28px;
            padding: 0 10px;
            font-size: 12px;
          }
          .#{$prefix-cls}button.#{$prefix-cls}button-secondary{
            border: none;
          }
          .#{$prefix-cls}button.#{$prefix-cls}button-primary{
            color: #fff;
            border: none;
            box-shadow: none;
          }
          .#{$prefix-cls}button.#{$prefix-cls}button-primary:hover{
            color: #fff;
          }
      }
  }
  .ac-confirm-body.#{$modal-prefix-cls}-body{
      padding: 32px 32px 16px 27px;
      min-height: 0;
      .ac-confirm-body-title{
          display: flex;
          align-items: center;

          .ac-confirm-body-title-icon{
              color: rgba(255, 191, 0, 1);
              font-size: 24px;
              line-height: 24px;
              vertical-align: sub;
              display: inline-block;

              &.uf-i-c {
                color: #4E97FF;
              }

              &.uf-correct {
                color: #18B681;
              }

              &.uf-exc-t,
              &.confirm-type {
                color: #FFA600;
              }

              &.uf-exc-c {
                color: #FF5735;
              }
          }

          .ac-confirm-body-title-keyword{
            margin-left: 9px;
            min-height: 21px;
            font-size: 16px;
            font-weight: 800;
            color: #333333;
            line-height: 21px;
          }
      }
      .ac-confirm-body-content{
          display: block;
          font-size: 14px;
          color: #666;
          margin: 8px 43px;

      }
  }

}
