@import "../../wui-core/scss/minxin-variables";
@import "../../wui-core/scss/minxin-mixins";
@import "../../wui-core/scss/util-keyframe";

$select-prefix-cls: $prefix-cls + 'select';

$select-dropdown-hover-bg: #f3f5f9;

  .#{$select-prefix-cls} {
      box-sizing: border-box;
      display: inline-block;
      position: relative;
      color: $select-color;
      width: 100%;
      font-size: $select-font-size;
      :root .#{$select-prefix-cls}-arrow {
          -webkit-filter: none;
          filter: none;
      }
      :root .#{$select-prefix-cls}-arrow {
        font-size: $select-font-size;
      }
      .#{$select-prefix-cls}-arrow {
          width: 16px;
          pointer-events: none;
          font-style: normal;
          vertical-align: baseline;
          text-align: center;
          text-transform: none;
          text-rendering: auto;
          text-rendering: optimizeLegibility;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          position: absolute;
          top: 50%;
          right: 8px;
          line-height: 1;
          margin-top: -6px;
          display: inline-block;
          color: $disabled-color-base;
          /* IE6-IE8 */
          -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=1, M12=0, M21=0, M22=1)";

          .uf-arrow-down,
          .uf-treearrow-down {
              padding: 0;
              font-size: 12px;
          }
      }
      .#{$select-prefix-cls}-arrow-loading {
        display: inline-block;
        width: 12px;
        height: 12px;
        border: 2px solid #eee;
        border-left-color: #aaa;
        border-radius: 50%;
        animation: loading 0.9s infinite linear;

        .uf-arrow-down {
            display: none;
        }
      }
      @keyframes loading {
        from { transform: rotate(0deg); }
        to { transform: rotate(360deg); }
      }
  }

  .#{$select-prefix-cls}-clear {
    display: inline-block;
    font-style: normal;
    vertical-align: baseline;
    text-align: center;
    text-transform: none;
    text-rendering: auto;
    opacity: 0;
    position: absolute;
    right: 28px;
    z-index: 1;
    background: #fff;
    top: 50%;
    font-size: 12px;
    color: #ccc;
    width: 12px;
    height: 12px;
    margin-top: -6px;
    line-height: 12px;
    cursor: pointer;
    -webkit-transition: color 0.3s ease, opacity 0.15s ease;
    transition: color 0.3s ease, opacity 0.15s ease;

    &-icon {
        display: none; // 不展示rc自带的icon
    }
    & .uf.uf-close-c {
      color: $clear-icon-base-color;
      font-size: $clear-icon-font-size;
      &:hover {
        color: $clear-icon-hover-color;
      }
    }
  }
  .#{$select-prefix-cls}-clear:hover,
  .#{$select-prefix-cls}-clear:hover {
    //color: #999;
    color: #505766;
  }
  .#{$prefix-cls}tree-select:hover .#{$select-prefix-cls}-clear,
  .#{$select-prefix-cls}:hover .#{$select-prefix-cls}-clear {
    opacity: 1;
  }
  .#{$select-prefix-cls}-selection-item {
    float: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 100%;
    padding-right: 14px;
    padding-left: 5px;
  }


  .#{$select-prefix-cls}-disabled {
      color: $select-disabled-color;

      .#{$select-prefix-cls}-clear {
          display: none;
          visibility: hidden;
          pointer-events: none;
      }
  }

//   .#{$select-prefix-cls}-lg {}
//   .#{$select-prefix-cls}-sm {}

  .#{$select-prefix-cls}:not(.#{$prefix-cls}tree-select) { // 同类名影响treeselect
    .#{$select-prefix-cls}-selection-placeholder {
      position: absolute;
      top: 50%;
      left: 8px;
      right: 9px;
      color: #ccc;
      line-height: 20px;
      height: 20px;
      max-width: 100%;
      margin-top: -10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  .#{$select-prefix-cls}-multiple {
    .#{$select-prefix-cls}-selector {
        .#{$select-prefix-cls}-selection-placeholder {
          margin-top: 0;
        }
        .#{$select-prefix-cls}-selection-search {
          margin-inline-start: 0px;
        }
    }

    .#{$select-prefix-cls}-clear {
        right: 11px;
    }
  }

  .#{$select-prefix-cls}-open {
      .#{$select-prefix-cls}-arrow {
          -ms-filter: "progid:DXImageTransform.Microsoft.BasicImage(rotation=2)";
        //   -ms-transform: rotate(180deg);
      }
      .#{$select-prefix-cls}-arrow {
          -webkit-transform: rotate(180deg);
          -ms-transform: rotate(180deg);
          transform: rotate(180deg);
          top: 50%;
      }
      .#{$select-prefix-cls}-selection {
          border-color: $brand-default;
          outline: 0;
      }
  }

  .#{$select-prefix-cls}-dropdown {
      padding: 8px 0;
      background-color: white;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.16);
      border-radius: $select-border-radius;
      box-sizing: border-box;
      z-index: 1800;
      left: -9999px;
      top: -9999px;
      position: absolute;
      outline: none;
      overflow: hidden;
      font-size: 12px;
      margin-bottom: 20px;
      animation: none;
      -webkit-animation: none;

      // -webkit-animation: uSlideUpIn 300ms ease-in;
      // animation: uSlideUpIn 300ms ease;
      &.slide-up-enter.slide-up-enter-active.#{$select-prefix-cls}-dropdown-placement-bottomLeft,
      &.slide-up-appear.slide-up-appear-active.#{$select-prefix-cls}-dropdown-placement-bottomLeft {
          -webkit-animation-name: uSlideUpIn;
          animation-name: uSlideUpIn;
      }
      &.slide-up-enter.slide-up-enter-active.#{$select-prefix-cls}-dropdown-placement-topLeft,
      &.slide-up-appear.slide-up-appear-active.#{$select-prefix-cls}-dropdown-placement-topLeft {
          -webkit-animation-name: uSlideDownIn;
          animation-name: uSlideDownIn;
      }
      &.slide-up-leave.slide-up-leave-active.#{$select-prefix-cls}-dropdown-placement-bottomLeft {
          -webkit-animation-name: uSlideUpOut;
          animation-name: uSlideUpOut;
      }
      &.slide-up-leave.slide-up-leave-active.#{$select-prefix-cls}-dropdown-placement-topLeft {
          -webkit-animation-name: uSlideDownOut;
          animation-name: uSlideDownOut;
      }
      &.#{$select-prefix-cls}-dropdown-hidden {
          display: none;
      }
      .#{$select-prefix-cls}-dropdown-menu {
          outline: none;
          margin-bottom: 0;
          padding-left: 0;
          list-style: none;
          max-height: 250px;
          overflow: auto;
          padding: 8px 0;
      }
      .#{$select-prefix-cls}-item-group-list {
          margin: 0;
          padding: 0;
          > .#{$select-prefix-cls}-item {
              padding-left: 24px;
          }
      }

      .#{$select-prefix-cls}-item-group-title {
          color: #999;
          line-height: 1.5;
          padding: 8px 16px;
      }
      // new css
      .#{$select-prefix-cls}-item {
          position: relative;
          display: block;
          padding: 7px 12px;
          padding-left: 12px;
          line-height: $line-height-small;
          font-weight: normal;
          color: $font-color-base;
          cursor: pointer;
          white-space: nowrap;
          overflow: hidden;
          -webkit-transition: background 0.3s ease;
          transition: background 0.3s ease;

          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;

          &-option {
              display: flex;

              &-state {
                  display: inline-block;
                  color: $brand-primary;
                  height: 18px;

                  i {
                      font-size: 14px;
                  }
              }

              &-grouped {
                  padding-left: 24px;
              }
          }

          &-option-content {
            flex: auto;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          &-group {
            color: #999;
            font-size: 12px;
            pointer-events: none;
          }
      }
      .#{$select-prefix-cls}-item:hover,
      .#{$select-prefix-cls}-item-option-active {
          background-color: $select-dropdown-hover-bg;
      }
      .#{$select-prefix-cls}-item-option-disabled {
          color: $select-disabled-color;
          cursor: not-allowed;
      }
      .#{$select-prefix-cls}-item-option-disabled:hover {
          color: $select-disabled-color;
          background-color: #fff;
          cursor: not-allowed;
      }
      .#{$select-prefix-cls}-item-option-selected,
      .#{$select-prefix-cls}-item-option-selected:hover {
          background-color: $select-dropdown-selected-bg;
          color: $font-color-base;
      }
      .#{$select-prefix-cls}-item-divider {
          height: 1px;
          margin: 1px 0;
          overflow: hidden;
          background-color: #e5e5e5;
          line-height: 0;
      }

      .#{$select-prefix-cls}-item-empty,
      .#{$select-prefix-cls}-empty { // treeselect的无搜索结果文字样式
        box-sizing: border-box;
        padding: 6px 10px;
        position: relative;
        display: block;
        height: 28px;
        line-height: 16px;
        color: #999;
        font-weight: normal;
        font-size: $select-font-size;
      }
  }
  .#{$select-prefix-cls}-dropdown.#{$select-prefix-cls}-dropdown--multiple {
      .#{$select-prefix-cls}-item:hover:after {
          color: #ddd;
      }
      .#{$select-prefix-cls}-item-disabled:after {
          display: none;
      }
      .#{$select-prefix-cls}-item-option-selected:after,.#{$select-prefix-cls}-item-option-selected:hover:after {
          color: $brand-primary;
          display: inline-block;
      }
  }



  :root .#{$select-prefix-cls}-dropdown.#{$select-prefix-cls}-dropdown--multiple .#{$select-prefix-cls}-item:after {
    -webkit-filter: none;
            filter: none;
  }
  :root .#{$select-prefix-cls}-dropdown.#{$select-prefix-cls}-dropdown--multiple .#{$select-prefix-cls}-item:after {
    font-size: 12px;
  }


  .#{$select-prefix-cls}-dropdown-container-open .#{$select-prefix-cls}-dropdown,
  .#{$select-prefix-cls}-dropdown-open .#{$select-prefix-cls}-dropdown {
    display: block;
  }
  .#{$prefix-cls}input-search-wrapper {
      display: inline-block;
      position: relative;
      .#{$prefix-cls}input-search {
          -webkit-transition: all .3s ease;
          transition: all .3s ease;
      }
      .#{$prefix-cls}input-search-icon {
          position: absolute;
          right: 8px;
          cursor: pointer;
          -webkit-transition: all .3s ease;
          transition: all .3s ease;
          font-size: 14px;
          height: 20px;
          line-height: 20px;
          top: 50%;
          margin-top: -10px;
      }
      .#{$prefix-cls}input-search-icon:hover {
          color: #108ee9;
      }
      &:hover .#{$prefix-cls}input-search:not[disabled] {
          border: 1px solid #108ee9;
      }
  }

  .#{$prefix-cls}search-input-wrapper {
    display: inline-block;
    vertical-align: middle;
  }
  .#{$prefix-cls}search-input {
      &.#{$prefix-cls}input-group {
          .#{$prefix-cls}input:first-child,.#{$select-prefix-cls}:first-child {
              border-radius: $select-border-radius;
              position: absolute;
              top: -1px;
              width: 100%;
          }
          .#{$prefix-cls}input:first-child {
              padding-right: 36px;
          }
      }
      .#{$prefix-cls}search-btn {
          color: #666;
          background-color: #f7f7f7;
          border-color: #d9d9d9;
          border-radius: 0 3px 3px 0;
          left: -1px;
          position: relative;
          border-width: 0 0 0 1px;
          z-index: 2;
          padding-left: 8px;
          padding-right: 8px;
      }

      .#{$prefix-cls}search-btn {
          > a:only-child {
              color: currentColor;
          }
          > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
          &:hover > a:only-child,&:focus > a:only-child {
              color: currentColor;
          }
          &:hover > a:only-child:after,&:focus > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
          &:active,&.active {
              color: #0f87dd;
              background-color: #f7f7f7;
              border-color: #0f87dd;
          }
          &:active> a:only-child,&.active> a:only-child {
              color: currentColor;
          }
          &:active> a:only-child:after,&.active> a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
          &:hover,&:focus {
              color: #40a5ed;
              background-color: #f7f7f7;
              border-color: #40a5ed;
          }
          &:active,&:hover,&:focus {
              background: #fff;
          }
          &:hover {
              border-color: #d9d9d9;
          }
      }
      .#{$prefix-cls}search-btn[disabled] {
          color: #ccc;
          background-color: #f7f7f7;
          border-color: #d9d9d9;
          &:hover,&:focus,&:active,&.active {
              color: #ccc;
              background-color: #f7f7f7;
              border-color: #d9d9d9;
          }
          & > a:only-child,&:hover > a:only-child,&:focus > a:only-child,&:active > a:only-child,&.active > a:only-child {
              color: currentColor;
          }
          & > a:only-child:after,&:hover > a:only-child:after,&:focus > a:only-child:after,&:active > a:only-child:after,&.active > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
      }
      .#{$prefix-cls}search-btn.disabled {
          color: #ccc;
          background-color: #f7f7f7;
          border-color: #d9d9d9;
          &:hover,&:focus,&:active,&.active {
              color: #ccc;
              background-color: #f7f7f7;
              border-color: #d9d9d9;
          }
          > a:only-child,&:hover > a:only-child,&:focus > a:only-child,&:active > a:only-child,&.active > a:only-child {
              color: currentColor;
          }
          > a:only-child:after,&:hover > a:only-child:after,&:focus > a:only-child:after,&:active > a:only-child:after,&.active > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
      }
      &.#{$prefix-cls}search-input-focus,&:hover {
          .#{$prefix-cls}search-btn-noempty {
              color: #fff;
              background-color: #108ee9;
              border-color: #108ee9;
              > a:only-child {
                  color: currentColor;
              }
              > a:only-child:after {
                  content: '';
                  position: absolute;
                  top: 0;
                  left: 0;
                  bottom: 0;
                  right: 0;
                  background: transparent;
              }
          }
          .#{$prefix-cls}search-btn-noempty:focus,.#{$prefix-cls}search-btn-noempty:hover {
              color: #fff;
              background-color: #40a5ed;
              border-color: #40a5ed;
          }
          .#{$prefix-cls}search-btn-noempty:hover > a:only-child,.#{$prefix-cls}search-btn-noempty:focus > a:only-child {
              color: currentColor;
          }
          .#{$prefix-cls}search-btn-noempty:hover > a:only-child:after,.#{$prefix-cls}search-btn-noempty:focus > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
          .#{$prefix-cls}search-btn-noempty:active,.#{$prefix-cls}search-btn-noempty.active{
              color: #fff;
              background-color: #0f87dd;
              border-color: #0f87dd;
          }
          .#{$prefix-cls}search-btn-noempty:active > a:only-child,.#{$prefix-cls}search-btn-noempty.active > a:only-child{
              color: currentColor;
          }
          .#{$prefix-cls}search-btn-noempty:active > a:only-child:after,.#{$prefix-cls}search-btn-noempty.active > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }

      }
  }
  .#{$prefix-cls}search-input.#{$prefix-cls}search-input-focus,.#{$prefix-cls}search-input:hover {
      .#{$prefix-cls}search-btn-noempty.disabled, .#{$prefix-cls}search-btn-noempty[disabled]{
          color: #ccc;
          background-color: #f7f7f7;
          border-color: #d9d9d9;
          &:hover,&:focus,&:active,&.active {
              color: #ccc;
              background-color: #f7f7f7;
              border-color: #d9d9d9;
          }
          > a:only-child,&:hover > a:only-child,&:focus > a:only-child,&:active > a:only-child,&.active > a:only-child {
              color: currentColor;
          }
          > a:only-child:after,&:hover > a:only-child:after,&:focus > a:only-child:after,&:active > a:only-child:after,&.active > a:only-child:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              background: transparent;
          }
      }
  }

  @-webkit-keyframes uSlideUpIn {
    0% {
          opacity: 0;
          -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
          -webkit-transform: scaleY(0.8);
          transform: scaleY(0.8);
    }
    100% {
          opacity: 1;
          -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
          -webkit-transform: scaleY(1);
          transform: scaleY(1);
    }
  }
