:root{--border-color: #d9d9d9;--form-control-color: #424242;--hover-bg-color-base: #EDF1F7;--date-picker-color: var(--form-control-color);--clear-icon-base-color: #CCC;--clear-icon-hover-color: #505766;--clear-icon-font-size: 16px}.wui-divider{border-top:1px solid #e9ebec}.wui-divider-vertical{position:relative;top:-0.06em;display:inline-block;height:.9em;margin:0 8px;vertical-align:middle;border-top:0;border-left:1px solid #e9ebec}.wui-divider-horizontal{display:flex;clear:both;width:100%;min-width:100%;margin:24px 0}.wui-divider-horizontal.wui-divider-with-text{display:flex;margin:16px 0;color:#333;font-weight:500;font-size:16px;white-space:nowrap;text-align:center;border-top:0;border-top-color:#e9ebec}.wui-divider-horizontal.wui-divider-with-text::before,.wui-divider-horizontal.wui-divider-with-text::after{position:relative;top:50%;width:50%;border-top:1px solid rgba(0,0,0,0);border-top-color:inherit;border-bottom:0;transform:translateY(50%);content:""}.wui-divider-horizontal.wui-divider-with-text-left::before{top:50%;width:5%}.wui-divider-horizontal.wui-divider-with-text-left::after{top:50%;width:95%}.wui-divider-horizontal.wui-divider-with-text-right::before{top:50%;width:95%}.wui-divider-horizontal.wui-divider-with-text-right::after{top:50%;width:5%}.wui-divider-inner-text{display:inline-block;padding:0 12px}.wui-divider-dashed{background:none;border-color:#e9ebec;border-style:dashed;border-width:1px 0 0}.wui-divider-horizontal.wui-divider-with-text.wui-divider-dashed{border-top:0}.wui-divider-horizontal.wui-divider-with-text.wui-divider-dashed::before,.wui-divider-horizontal.wui-divider-with-text.wui-divider-dashed::after{border-style:dashed none none}.wui-divider-vertical.wui-divider-dashed{border-width:0 0 0 1px}